import User from "./modules/User";

// Pour r√©cup√©rer de l'API que le titre, pr√©nom, nom, email, ville, code postal, pays, num√©ro de t√©l√©phone, cellulaire, date de naissance, √¢ge, genre, photo de profil, et nationalit√©
// Tableau vide pour stocker les utilisateurs
const tableauUser = [];

// Fonction pour r√©cup√©rer les utilisateurs depuis l'API
const getUsers = () => {

    //üôã‚Äç‚ôÄÔ∏è¬†R√©cup√©rer des profils utilisateurs depuis une API (6 points)
    // Appel √† l'API pour obtenir 20 utilisateurs al√©atoires
    const users = fetch("https://randomuser.me/api/?results=20").then((resutlat) => resutlat.json());

    // Traitemnt des donn√©es une fois qu'elles sont r√©cup√©rers
    users.then((data) => {
        const { results } = data;
        // pour chaque √©l√©ment (utilisateur) dans le tableau results provenant de l'API, un nouvel objet User est cr√©√©.
        // Boucle pour r√©cup√©rer les donn√©es souhait√©es
        results.forEach((element) => {
            tableauUser.push(
                new User(
                    element.name.title,
                    element.name.first,
                    element.name.last,
                    element.location.city,
                    element.location.country,
                    element.dob.age,
                    element.email,
                    element.picture.large
                )

            );
        });

        //  Tri du tableauUser par ordre alphab√©tique des noms, trie de A-Z
        tableauUser.sort((a, b) => {
            //  compare les noms (nom) de deux utilisateurs (a et b) en utilisant localeCompare pour effectuer un tri sensible √† la casse et aux accents.
            return a.nom.localeCompare(b.nom);
        });
        // Affichage de chaque utilisateur dans le document HTML
        tableauUser.forEach((element) => {
            element.render();
        });
    });
};

// Appel de la fonction pour r√©cup√©rer les utilisateurs
getUsers();


// Gestionnaire d'√©v√©nements pour le tri des utilisateurs
document.querySelector(".filters").addEventListener("click", (event) => {
    // V√©rification si la cible de l'√©v√©nement n'a pas d√©j√† la classe "selected"
    if (!event.target.classList.contains("selected")) {
        // V√©rification de l'ID de la cible de l'√©v√©nement pour d√©terminer le type de tri
        if (event.target.id === "sort--name") {
            // Suppression de la classe "selected" de l'√©l√©ment actuellement s√©lectionn√©
            document.querySelector(".selected").classList.remove("selected");
            // Ajout de la classe "selected" √† l'√©l√©ment sur lequel l'utilisateur a cliqu√©
            event.target.classList.add("selected");

            // Tri du tableauUser par ordre alphab√©tique des noms
            tableauUser.sort((a, b) => {
                return a.nom.localeCompare(b.nom);
            });
            // Effacement du contenu principal dans le document HTML
            document.querySelector("main").innerHTML = "";

            // Affichage de chaque utilisateur dans le document HTML
            tableauUser.forEach((element) => {
                element.render();
            });

        } else if (event.target.id === "sort--age") {
            // Suppression de la classe "selected" de l'√©l√©ment actuellement s√©lectionn√©
            document.querySelector(".selected").classList.remove("selected");
            // Ajout de la classe "selected" √† l'√©l√©ment sur lequel l'utilisateur a cliqu√©
            event.target.classList.add("selected");

            // Tri du tableauUser par ordre croissant des √¢ges
            tableauUser.sort((a, b) => {
                return a.age - b.age;
            });
            // Effacement du contenu principal dans le document HTML
            document.querySelector("main").innerHTML = "";

            // Affichage de chaque utilisateur dans le document HTML
            tableauUser.forEach((element) => {
                element.render();
            });
        }

    }
});
