// Cr√©ez un module User.js qui exporte une classe User
//avec les propri√©t√©s et les m√©thodes suivantes. √Ä vous de les nommer et de les impl√©menter
//- Une ou plusieurs propri√©t√©s contenant les informations d‚Äôun utilisateur (nom, √¢ge, email, photo etc‚Ä¶).
//Une propri√©t√© indiquant si l‚Äôutilisateur est pr√©sent ou non. Celle-ci devrait √™tre `false` par d√©faut.
// Une propri√©t√© se r√©f√©rant √† l‚Äô√©l√©ment utilisateur qui sera g√©n√©r√© par la m√©thode d√©crite directement **ci-dessous.**

// propri√©t√© priv√©es de la classe
class User {
    #titre;
    #prenom;
    nom;
    #ville;
    #pays;
    age;
    #email;
    #photo;
    #present;
    #element;

    // Propri√©t√© statique pour compter le nombre de personnes pr√©sentes
    static compteurPresence = 0;

    constructor(titre, prenom, nom, ville, pays, age, email, photo) {
        // Initialisation des propri√©t√©s priv√©es et publiques
        this.#titre = titre;
        this.#prenom = prenom;
        this.nom = nom;
        this.#ville = ville;
        this.#pays = pays;
        this.age = age;
        this.#email = email;
        this.#photo = photo;
        this.#present = false;
        this.#element = this.#generateElement();

        // Ajout d'un √©couteur d'√©v√©nements pour g√©rer le clic sur l'√©l√©ment utilisateur
        this.#element.addEventListener("click", (event) => {
            this.#togglePresence(event.currentTarget);
        });

    }

    // M√©thode pour g√©n√©rer un √©l√©ment utilisateur, 
    // elle est utilis√©e pour cr√©er et g√©n√©rer un √©l√©ment HTML repr√©sentant les informations d'un utilisateur. 
    #generateElement() {
        // un nouvel √©l√©ment div est cr√©√©
        const div = document.createElement("div");
        // ajout de la classe user
        div.classList.add("user");
        // ajout de l'attribut data-present
        div.dataset.present = this.#present;
        // construction du contenu HTML √† l'int√©reiur de la div
        /* Un bloc de texte est cr√©√© avec des balises HTML. Il contient une image, des informations de l'utilisateur telles que le nom, l'√¢ge, la ville et le pays, ainsi qu'un lien de messagerie √©lectronique.
        Les informations de l'utilisateur sont obtenues √† partir des propri√©t√©s priv√©es (#titre, #prenom, nom, age, #ville, #pays, #email) de l'objet User.  */
        const contenu = `
        <img src="${this.#photo}">
		<div class="user--info">
				<h1>${this.#titre} ${this.#prenom} ${this.nom}</h1>
				<p>${this.age} years old</p>
				<p>${this.#ville}, ${this.#pays}</p>
		</div>
        <a href="mailto:${this.#email}">
			<span class="mail">‚úâÔ∏è</span>
		</a>`;
        // Le contenu HTML construit est ins√©r√© dans l'√©l√©ment <div> nouvellement cr√©√©. L'option "afterbegin" signifie que le contenu sera ins√©r√© au d√©but de l'√©l√©ment.
        div.insertAdjacentHTML("afterbegin", contenu);

        return div;
    }

    // M√©thode d‚Äôaffichage des √©l√©ments utilisateurs
    render() {
        document.querySelector("main").appendChild(this.#element);
    }


    // M√©thode d‚Äôinversion de pr√©sence
    #togglePresence(div) {
        // V√©rification de l'√©tat de pr√©sence actuel
        if (this.#present) {
            // Changement de l'√©tat de pr√©sence et mise √† jour du compteur
            // La propri√©t√© dataset.present de l'√©l√©ment <div> est mise √† jour pour refl√©ter le nouvel √©tat.
            div.dataset.present = false;
            // La propri√©t√© priv√©e #present de l'objet User est mise √† jour.
            this.#present = false;
            User.compteurPresence--;
        } else {
            // Changement inverse de l'√©tat de pr√©sence et mise √† jour du compteur
            div.dataset.present = true;
            this.#present = true;
            User.compteurPresence++;
        }
        // Mise √† jour du compteur d'utilisateurs pr√©sents
        document.querySelector(
            ".counter"
        ).textContent = `${User.compteurPresence}/20 people are here`;
    }

}

export default User;


